import { My } from "@/api/shop/index.js"
import {
	EventBus
} from "@/common/bus.js";
export default {
	data() {
		return {
			info:{
				page:1,
				limit:10,
				list:[],
			}
		};
	},
	created() {
		EventBus.$on("reloadData-myShop-list", () => {
			this.info.page=1;
			this.info.list=[];
			this.loadData();
		});
		
		this.loadData();
	},
	methods: {
		loadData(){
			const uid = this.$store.getters.uid
			My(uid,this.info.page,this.info.limit,info=>{
				this.info.list=this.info.list.concat(info.list)
			})
		},
		handleClick(item){
			if(item.status==0){
				uni.navigateTo({
					url: "/pages/my/shop/edit?id="+item.id
				})
			}
		},
		toAdd() {
			uni.navigateTo({
				url: "/pages/my/shop/add"
			})
		}
	}
}
